<template>
	<div class="body fundManage_body">
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%">
					<el-table-column prop="" label="序号" sortable width="100">
						<template slot-scope="scope">
							{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
						</template>
					</el-table-column>
					<el-table-column prop="subscribeshortname" label="认购基金简称" sortable width="150">
					</el-table-column>
					<el-table-column prop="subscribename" label="认购基金全称" sortable width="200">
					</el-table-column>
					<el-table-column prop="fair_value" label="公允价值" sortable width="200">
					</el-table-column>
					<el-table-column prop="exit_date" label="最近退出日期" sortable width="200">
					</el-table-column>
					<el-table-column prop="exit_status" label="退出状态" sortable width="200">
					</el-table-column>
					<el-table-column prop="is_change" label="是否确权" sortable width="180">
						<template slot-scope="scope">
							{{ scope.row.is_change == '1'?'是':'否' }}
						</template>
					</el-table-column>
					<el-table-column prop="subscribe_fee" label="认缴（万元）" sortable width="180">
					</el-table-column>
					<el-table-column prop="feal_fee" label="实缴（万元）" sortable width="180">
					</el-table-column>
					<el-table-column prop="real_rate" label="实缴进度" sortable width="120">
						<template slot-scope="scope">
							{{ scope.row.real_rate }}&nbsp;%
						</template>
					</el-table-column>
					<el-table-column prop="multiple" label="倍数" sortable width="120">
					</el-table-column>
					<el-table-column prop="update_time" label="更新时间" sortable width="180">
					</el-table-column>
					<el-table-column prop="irr" label="XIRR" sortable width="120">
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" sortable width="180">
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" sortable width="200">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="100">
						<template slot-scope="scope">
							<div class="" v-if="scope.row.id">
								<el-button type="text" size="small" @click="viewFund(scope.row)">查看</el-button>
								<!--<el-button type="text" size="small" @click="deleteFund(scope.row)">删除</el-button>-->
							</div>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span> 
						</div>
					</template>
				</el-table>
				<div class="" v-if="total">
					<el-pagination @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="total">
    					</el-pagination>
				</div>
			</div>
		</div>

	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getRunSubscriptionFundListApi,deleteRunSubscriptionFundListApi } from '../../config/api'

	export default {
		name: 'subFundRunSubscribe',
		data() {
			return {
				id: '',
				currentPage: 1,
				pageSize: 10,
				total: 0,
				tableData: [],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getInitData()
		},
		mounted() {},
		components: {},
		methods: {
			getInitData(){
				getRunSubscriptionFundListApi({
					fund_id: this.id,
					page: this.currentPage,
					pageSize: this.pageSize,
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.total = res.data.payload.total;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			viewFund(e){
				var type = e.subscribe_type;
				if (type == 1) {
					var name = 'motherFundCompanyDetail';
				} else{
					var name = 'selfAdministerFundCompanyDetail';
				}
				this.$router.push({
					name: name,
					query: { id: e.fof_id }
				})
			},
			deleteFund(e) {
				dialogConfirmHasbtn('确定要删除所选基金吗?').then(() => {
					deleteRunSubscriptionFundListApi({
						id: e.id,
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", res.data.message);
							this.getInitData()
						} else {
							dialogConfirm("info", res.data.message);
						}
					})
				}).catch(() => {});
			},
			// 分页功能模块
			changeCurrentPage(e){
				this.currentPage = e;
				this.getInitData()
			},
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getInitData()
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 0px 30px 0px;
	}
</style>


